

export const calculate = (location, values, setValues) => {
    switch (location) {
        case 'd12':
            if (values.d10 && values.d11) {
                setValues({ ...values, d12: event.target.value });
                return values.d10 - values.d11
            }
        case 'd23':
            if (values.d21 && values.d22) {
                return values.d21 - values.d22
            }
        default:
            return ''
    }
}